.Rating {
  text-align: center;
  position: relative;
  width: 100%;
  height: 100%;

  h2 {
    padding: 0;
    margin-bottom: 0;
    font-size: 2.4em;
  }

  h3 {
  }

  p {
    padding: 10px 0;
    margin-bottom: 20px;
    font-size: 1.2em;
  }

  .button-container {
    position: fixed;
    bottom: 40px;
    width: 100%;
  }

  .hide {
    visibility: hidden;
    display: none;
  }

  .show {
    visibility: visible;
    display: block;
  }
}
